import React from 'react'
import Shimmer from './Shimmer';
import ItemCard from './MenuItemCard';
import { useParams } from 'react-router-dom';
import useMenu from '../utils/useMenu';

const Menu = () => {

  const {resID} = useParams();
  const resinfo = useMenu(resID);
 
  const {name, costForTwoMessage, cuisines, avgRating, totalRatings} = resinfo?.cards[2]?.card?.card?.info || {};
  const {itemCards} = resinfo?.cards[4]?.groupedCard?.cardGroupMap?.REGULAR?.cards[2]?.card?.card   || {};

  return (resinfo == null )? <Shimmer/> : (<>
      <div className='resaurant-menu-details-container'>
        <div className='restaurant-menu-details-card'>
          <h1>{name}</h1>
          <div>{"Cuisine: "+cuisines.join(" , ")}</div>
          <div>{costForTwoMessage}</div>
          <div>{avgRating + " (" + totalRatings + " Ratings)"}</div>
          <h3>Recommended</h3>
        </div>
      </div>
      <div className='menu-container'>
            {
              itemCards.map((item) => <ItemCard key={item.card.info.id} menuItem={item} />)
            }
      </div>
    </>
  )
};

export default Menu;