import RestaurantCard from "./RestaurantCard";
import Shimmer from "./Shimmer"
import { useState, useEffect} from "react";
import { Link } from "react-router-dom";
import useOnlineStatus from "../utils/useOnlineStatus";

const Body = () => {

    const [resList, setresList] = useState([]);
    const [filterResList, setfilterResList] = useState([]);
    const [searchInput, setsearchInput] = useState("");

    useEffect(()=>{
        fetchData();
    },[])
    
    const fetchData = async () => {
    try{
            const data = await fetch(
            "https://www.swiggy.com/dapi/restaurants/list/v5?lat=28.4743207&lng=77.508911&is-seo-homepage-enabled=true&page_type=DESKTOP_WEB_LISTING"
        );     
        const json = await data.json();
        const restuarants = json?.data?.cards[1]?.card?.card?.gridElements?.infoWithStyle?.restaurants
        if(restuarants){
        setresList(restuarants);
        setfilterResList(restuarants);
        }
        else{
            console.log("Unexpected JSON structure", json);
        }
    }
    catch(err){
            console.log("Failed to fetch Data", err);
        }
    };

    const onlineStatus = useOnlineStatus();

    if(onlineStatus === false){
        return(<>
            <h1>Looks like you are offline!!</h1>
            <h1>Please check your internet connection Bitch.</h1>
            </>
        )
    };

    function handlefilterbtn(){
        const newList = resList.filter((res) => res.info.avgRating>=4);
        setfilterResList(newList);
    }

    function handleRemoveFilterBtn(){
        setfilterResList(resList);
    }

    return (resList.length === 0) ? (<Shimmer/>) : (  

    <div className="body-container">
        <div className="search-container">
            <input className="search-bar" type="text" placeholder="Search Your 'Favourite Restaurant' Here" value={searchInput}
                onChange={(e)=>{setsearchInput(e.target.value)}}
            />
            <button className="search-btn"
                onClick={()=>{
                    console.log(searchInput)
                    const newList = resList.filter((res) => 
                        res.info.name.toLowerCase().includes(searchInput.toLowerCase())
                    );
                    if (newList.length === 0) {
                        alert("No restaurants found with the given search input");
                    }
                    else{
                        setfilterResList(newList);
                    } 
                }
            }
            >Search</button>
        </div>

        <div>
            <button className="filter-btn"
                onClick={()=>handlefilterbtn()}>Top Rated Restaurants
            </button>

            <button className="remove-filter-btn"
                onClick={()=>handleRemoveFilterBtn()}>Remove Filter
            </button>
        </div>
        

        <div className="restra-container">
            {
                filterResList.map((restaurant, index) => <Link key={restaurant.info.id} to={"http://localhost:1234/restaurant/" + restaurant.info.id}><RestaurantCard resData={restaurant}/></Link>)
            }
        </div>
    </div>
    )
}

export default Body;