const ItemCard = (props) => {
    const {menuItem} = props;
    const {name, description, price, imageId, ratings} = menuItem?.card?.info || {};
    
    return (
        <div className="menu-item-card">
            <div className="details-section">
                <div>{name}</div>
                <div>{price/100 || "not given"}</div>
                <div>{ratings.aggregatedRating.rating +"("+ratings.aggregatedRating.ratingCountV2+")" || "rating not given"}</div>
                <div>{description}</div>
            </div>
            <div className="image-section">
                <img src={"https://media-assets.swiggy.com/swiggy/image/upload/fl_lossy,f_auto,q_auto,w_300,h_300,c_fit/"+imageId} alt="item-img" />
            </div>
        </div>
    )
};

export default ItemCard;