import { IMG_URL } from "../utils/constants";

const RestaurantCard = (props) => {
    const {resData} = props;
    const {name,cuisines,locality,areaName,avgRatingString,sla,cloudinaryImageId} = resData?.info
    return (
        <div className="restra-card">
            <img 
                className="restra-img" 
                alt="restra-img" 
                src = {IMG_URL + cloudinaryImageId}
            />
            <h3>{name}</h3>
            <h5>{cuisines.join(', ')}</h5>
            <h5>{locality + ", " + areaName}</h5>
            <div className="restra-data">
                <h5>{avgRatingString + " ratings"}</h5>
                <h5>{sla.slaString}</h5>
            </div>
        </div>
    )
};

export default RestaurantCard;