import { LOGO_URL } from "../utils/constants";
import { useState } from "react";
import { Link } from "react-router-dom";
import useOnlineStatus from "../utils/useOnlineStatus";

const Header = () => {
    const [btnName, setbtnName] = useState("Login/Signup");

    const onlineStatus = useOnlineStatus();

    return (
        <div className="Header-container">
            <Link to="/"><img className="logo-img" alt="logo" src= {LOGO_URL} /></Link>
            <p  className="online-status">Online status : {onlineStatus ? "": ""}</p>
            <div className="navbar">
            {/* <a href="/About"><button id="navbar-btns">Home</button></a>  //we do not use this because it reloads the whole page */}
                <Link to="/"><button id="navbar-btns">Home</button></Link>
                <Link to="/grocery"><button id="navbar-btns">Grocery</button></Link>
                <Link to="/About"><button id="navbar-btns">About Us</button></Link>
                <Link to="/Contact"><button id="navbar-btns">Contact Us</button></Link>
                <Link to="/MyCart"><button id="navbar-btns">My Cart</button></Link>
                <button className="login-btn"
                onClick={()=>{btnName==="Login"? setbtnName("LogOut") : setbtnName("Login")}}
                >{btnName}</button>
            </div>
        </div>
    )
}

export default Header;